<template>
  <div class='video-outer'>
    <div class="alpha">
      <div class="logo-outer">
        <img class='logo' src="../assets/logo.png">
      </div>
    </div>
    <div class="video-background">
      <youtube
        class="video-foreground"
        frameborder="0"
        allowfullscreen
        loop="1"
        :playerHeight="height"
        :playerWidth="width"
        :video-id="videoId"
        :player-vars="{ autoplay: 1, start: 30, showinfo: 0, rel: 0, loop: 1, controls: 0, playlist:'2Yas5UZZldI' }"
        :mute="true">
      </youtube>
    </div>
  </div>
</template>


<script>
  export default {
    name: 'masthead',
    data () {
      return {
        videoId: '2Yas5UZZldI',
        width: '1920px',
        height: '1080px',
        methods: {
          ready (player) {
            this.player = player
          },
          playing (player) {
            // The player is playing a video.
          },
          change () {
            // when you change the value, the player will also change.
            // If you would like to change `playerVars`, please change it before you change `videoId`.
            // If `playerVars.autoplay` is 1, `loadVideoById` will be called.
            // If `playerVars.autoplay` is 0, `cueVideoById` will be called.
            this.videoId = 'another video id'
          },
          stop () {
            this.player.stopVideo()
          },
          pause () {
            this.player.pauseVideo()
          }
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  div.logo-outer {
    height: 100vh;
    line-height: 100vh;
  }
  img.logo {
    width: 80%;
    max-width: 500px;
    vertical-align: middle;
  }
  @media screen and (min-width: 768px) {
    div.logo-outer {
      height: 700px;
      line-height: 700px;
    }
    img.logo {
      width: 50%;
    }
  }
  div.alpha {
      background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAIAAAACCAYAAABytg0kAAAAGklEQVQImWNgYGBYZmJiMoXBxMRkCgMDwzIAHgwDrawJG44AAAAASUVORK5CYII=);
      background-repeat: repeat;
      width: 100%;
      position:absolute;
      height: 100vh;
      top:0;
      z-index: 10;
  }
  @media screen and (min-width: 768px) {
    div.alpha {
      height: 700px;
    }
  }
  .video-background {
    background: #000;
    background-image: url('../assets/hornet.jpg');
    background-position: center;
    background-size:cover;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 5;
    height: 100vh;
    overflow: hidden;
  }
  .video-foreground, .video-background iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    pointer-events: none;
    display: none;
  }
  .video-background iframe {
    display: none;
  }
  @media screen and (min-width: 768px) {
    .video-foreground, .video-background iframe, .video-background {
      height: 700px;
      display: block;
    }
    .video-background iframe {
      display: block;
      height: 200% !important;
      margin-top: -350px;
    }
  }
  #vidtop-content {
    top: 0;
    color: #fff;
  }
  .vid-info {
    position: absolute;
    top: 0;
    right: 0;
    width: 33%;
    background: rgba(0, 0, 0, 0.3);
    color: #fff;
    padding: 1rem;
    font-family: Avenir, Helvetica, sans-serif;
  }
  .vid-info h1 {
    font-size: 2rem;
    font-weight: 700;
    margin-top: 0;
    line-height: 1.2;
  }
  .vid-info a {
    display: block;
    color: #fff;
    text-decoration: none;
    background: rgba(0, 0, 0, 0.5);
    -webkit-transition: .6s background;
    transition: .6s background;
    border-bottom: none;
    margin: 1rem auto;
    text-align: center;
  }
  @media (min-aspect-ratio: 16/9) {
    .video-foreground {
      height: 300%;
      top: -150px;
    }
  }
  @media (max-aspect-ratio: 16/9) {
    .video-foreground {
      width: 300%;
      left: -100%;
      top: -150px;
    }
  }
  @media all and (max-width: 600px) {
    .vid-info {
      width: 50%;
      padding: .5rem;
    }
    .vid-info h1 {
      margin-bottom: .2rem;
    }
  }
  @media all and (max-width: 500px) {
    .vid-info .acronym {
      display: none;
    }
  }
</style>



